/* feedback */
.feedback {
    padding-right: 8px;
    padding-left: 8px;
    @include bg-color-section();
}
.feedback__inner {
    position: relative;
    padding-top: 40px;
    padding-bottom: 40px;
    background: $cyan;
    color: $white;
    border-radius: $border-radius-8;
    @include xl {
        padding-top: 120px;
        padding-bottom: 118px;
    }
    &::before,
    &::after {
        content: "";
        position: absolute;
        background-color: transparent;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }
    &::after {
        right: 0;
        bottom: 0;
        width: 230px;
        height: 62px;
        background-image: url("data:image/svg+xml,%3Csvg width='230' height='62' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg clip-path='url(%23clip0_284_10849)'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M199.422 21.93c6.056 0 10.965-4.91 10.965-10.965C210.387 4.909 205.478 0 199.422 0s-10.965 4.91-10.965 10.965 4.909 10.965 10.965 10.965z' fill='%236665DD'/%3E%3Cpath opacity='.32' d='M230.26 8.682c-124.88-9.417-63.752 129.646-49.127 105.475C211.64 63.739 0 73.455 0 159.675' stroke='%23fff' stroke-width='2' stroke-linecap='round' stroke-dasharray='5 5'/%3E%3C/g%3E%3Cdefs%3E%3CclipPath id='clip0_284_10849'%3E%3Cpath fill='%23fff' transform='matrix(-1 0 0 1 230 0)' d='M0 0h230v62H0z'/%3E%3C/clipPath%3E%3C/defs%3E%3C/svg%3E");
        @include xl {
            right: auto;
            left: 0;
            width: 338px;
            height: 234px;
            background-image: url("data:image/svg+xml,%3Csvg width='338' height='234' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M46 32c-8.837 0-16-7.163-16-16S37.163 0 46 0s16 7.163 16 16-7.163 16-16 16z' fill='%236665DD'/%3E%3Cpath fill='%236665DD' d='M242.956 134l-10.955 7.111 7.114 10.95 10.956-7.11z'/%3E%3Cpath opacity='.32' d='M1 12.669C183.228-1.073 94.03 201.851 72.687 166.58 28.171 93.01 337 107.187 337 233' stroke='%23fff' stroke-width='2' stroke-linecap='round' stroke-dasharray='5 5'/%3E%3C/svg%3E");
        }
    }
    &::before {
        @include xl {
            top: 0;
            right: 0;
            width: 258px;
            height: 174px;
            background-image: url("data:image/svg+xml,%3Csvg width='258' height='174' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill='%236665DD' d='M110.115 82.061l10.955-7.11L113.956 64 103 71.11z'/%3E%3Cpath opacity='.32' d='M257 172.78c-138.006 6.532-83.85-134.866-54.323-119.996C263.954 83.644-21.151 89.154 2.385 1' stroke='%23fff' stroke-width='2' stroke-linecap='round' stroke-dasharray='5 5'/%3E%3C/svg%3E");
        }
    }
}
.container--feedback {
    max-width: 1120px;
}
.feedback__title {
    max-width: 600px;
    margin-bottom: 40px;
    @include xl {
        margin-bottom: 0;
    }
}
.feedback__top {
    position: relative;
    z-index: 1;
    margin-bottom: 40px;
    @include xl {
        @include fl();
        @include fl-between();
        @include fl-v-centr();
        margin-bottom: 56px;
    }
}
.feedback__bottom {
    position: relative;
    z-index: 1;
 }
.feedback__btn {
    // width: 100%;
    @media(min-width: 400px) {
        padding-right: 70px;
        padding-left: 70px;
        max-width: 224px;
    }
}
.feedback__list {
    position: relative;
    z-index: 1;
    @include md {
        @include fl();
        @include fl-between();
    }
    font-size: 16px;
    font-weight: 500;
    line-height: 1.4444444444444444;
    div {
        &::before {
            top: 8px;
            background-image: url("data:image/svg+xml,%3Csvg width='12' height='10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M3.372 9.788A.641.641 0 003.84 10a.615.615 0 00.467-.212l7.497-7.855A.703.703 0 0012 1.437a.727.727 0 00-.197-.496L11.096.2a.652.652 0 00-.467-.2.652.652 0 00-.467.2L3.839 6.825l-2-2.083a.641.641 0 00-.468-.211.615.615 0 00-.467.211l-.707.741A.702.702 0 000 5.98a.728.728 0 00.197.497l3.175 3.312z' fill='%23ffffff'/%3E%3C/svg%3E");
        }
    }
    @include lg {
        font-size: 18px;
    }
}
