/* governance */
.hero--governance {
    &::before {
        display: none;
    }
    .hero__row {
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-between;
        justify-content: space-between;
        -webkit-align-items: center;
        align-items: center;
        img {
            margin-left: 0;
            @include md {
                max-width: 304px;
            }
            @include xl {
                position: relative;
                top: -12px;
            }
        }
        .hero__text {
            max-width: 615px;
            margin-right: auto;
            margin-left: auto;
            text-align: center;
        }
        .hero__title {
            margin-top: 37px;
        }
    }
    .hero__text--d {
        @include max-xl {
            display: none;
        }
    }
    .hero__text--m {
        @include xl {
            display: none;
        }
    }
    .hero__1 {
        @include max-xl {
            padding-bottom: 0;
        }
    }
    .hero__2 {
        padding-top: 100px;
        padding-bottom: 40px;
        @include xl {
            padding-top: 113px;
            padding-bottom: 106px;
        }
        .hero__title {
            max-width: 560px;
            margin-top: 0;
            margin-bottom: 16px;
        }
        .hero__content {
            line-height: 1.777777777777778;
            @include xl {
                font-size: 18px;
                padding-left: 20px;
            }
        }
    }
    .hero__img--right {
        margin-right: -46px;
        @include md {
            margin-right: 0;
        }
    }
    .hero__img--left {
        margin-left: -46px;
        @include md {
            margin-left: 0;
        }
    }
}
/* commitment */
.commitment {
    @include bg-color-section();
}
.commitment__inner {
    @include max-xl {
        padding-top: 56px;
        padding-bottom: 56px;
    }
    @include md {
        border-radius: $border-radius-8;
    }
    @include xl {
        padding-top: 120px;
        padding-bottom: 100px;
    }
    background: $soft-gray;
}
.container--commitment {
    max-width: 1120px;
}
.commitment__title {
    max-width: 800px;
    text-align: center;
    margin-bottom: 40px;
    margin-right: auto;
    margin-left: auto;
    color: $dark-1;
    @include xl {
        margin-bottom: 48px;
    }
    br {
        @include max-md {
            display: none;
        }
    }
}
/* tile */
.tile {
    font-size: 18px;
    font-weight: 500;
    line-height: 1.6;
    color: $dark-1;
    @include xl {
        font-size: 20px;
    }
    margin-right: -16px;
    margin-left: -16px;
    @include xl {
        @include fl();
    }
}
.tile__col-1 {
    @include xl {
        -webkit-flex: 0 1 60%;
        flex: 0 1 60%;
        max-width: 60%;
        min-width: 60%;
        width: 60%;
    }
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
    flex-direction: column;
    .tile__inner {
        @include xl {
            @include fl();
        }
    }
    .tile__img {
        .tile__content {
            @include max-xl {
                padding-top: 16px;
            }
        }
    }
    .tile__text {
        .tile__inner {
            height: 100%;
            padding: 16px 24px;
            @include xl {
                padding: 32px 40px;
            }
        }
    }
    img {
        @include lg {
            flex: 0 1 176px;
            max-width: 176px;
            min-width: 176px;
            width: 176px;
            height: 208px;
        }
        @include xl {
            margin-right: 25px;
        }
    }
    .tile__inner--v {
        color: $white;
        @include md {
            @include fl();
            @include fl-v-centr();
        }
    }
}
.tile__col-2 {
    @include md {
        text-align: center;
    }
    @include xl {
        -webkit-flex: 0 1 40%;
        flex: 0 1 40%;
        max-width: 40%;
        min-width: 40%;
        width: 40%;
        text-align: left;
    }
    .tile__img {
        .tile__content {
            @include max-xl {
                padding-bottom: 16px;
                padding-right: 8px;
                padding-left: 8px;
            }
        }
    }
    .tile__text {
        .tile__inner {
            padding: 24px;
            @include xl {
                padding: 40px 48px;
            }
        }
    }
    img {
        @include md {
            flex: 0 1 384px;
            max-width: 384px;
            min-width: 384px;
            width: 384px;
            height: 80px;
            margin-right: auto;
            margin-left: auto;
        }
    }
}
.tile__row {
    @include md {
        @include fl();
        -webkit-order: 1;
        order: 1;
        -webkit-align-content: stretch;
        -ms-flex-line-pack: stretch;
        align-content: stretch;
        -webkit-align-items: stretch;
        -ms-flex-align: stretch;
        align-items: stretch;
    }
    .tile__item {
        height: auto;
        @include md {
            -webkit-flex: 0 1 50%;
            flex: 0 1 50%;
            width: 50%;
            max-width: 50%;
            min-width: 50%;
        }
    }
    .tile__content {
        @include md {
            text-align: center;
        }
        @include xl {
            text-align: left;
        }
    }
}
.tile__text {
    br {
        @include max-xl {
            display: none;
        }
    }
}
.tile__item {
    padding: 8px 16px;
    @include md {
        padding-right: 8px;
        padding-left: 8px;
    }
    @include xl {
        padding: 16px;
    }
}
.tile__inner {
    box-shadow: 0 8px 32px rgba(227, 227, 235, 0.24);
    border-radius: $border-radius-4;
    .tile__img & {
        padding: 16px;
    }
}
.tile__inner--w {
    background: $white;
}
.tile__inner--v {
    background: $light-violet;
}
.tile__img {
    .tile__content {
        @include md {
            padding-left: 16px;
        }
        @include xl {
            padding: 24px 32px;
        }
    }
}

/* commitment-quote */
.commitment-quote {
    margin-right: auto;
    margin-left: auto;
    padding-top: 40px;
    padding-bottom: 40px;
    text-align: center;
    @include xl {
        padding-top: 120px;
        padding-bottom: 120px;
    }
    @include xxl {
        max-width: 940px;
    }

}
.commitment-quote__title {
    font-size: 24px;
    line-height: 1.25;
    font-weight: 700;
    color: $dark-1;
    margin-bottom: 16px;
    @include xl {
        font-size: 32px;
        margin-bottom: 25px;
    }
}
.commitment-quote__content {
    font-size: 16px;
    line-height: 1.777777777777778;
    @include xl {
        font-size: 18px;
        color: $cyan;
    }
}
.commitment-quote__star {
    position: relative;
    font-size: 26px;
    margin-bottom: 24px;
    color: $cyan;
    &::before {
        content: "";
        position: absolute;
        top: 4px;
        right: 0;
        left: -30px;
        z-index: -1;
        margin: auto;
        width: 8px;
        height: 8px;
        border-radius: $border-radius-round;
        background: $blue;
    }
    @include xl {
        margin-bottom: 11px;
    }
}

/* experts */
.experts {
    @include bg-color-section();
}
.experts__inner {
    @include max-xl {
        padding-top: 40px;
        padding-bottom: 40px;
    }
    @include md {
        border-radius: $border-radius-8;
    }
    @include xl {
        padding-top: 120px;
        padding-bottom: 120px;
    }
    background: $soft-gray;
}
.grid--experts {
    .grid__item {
        &:first-child {
            @include max-xl {
                margin-bottom: 32px;
            }
            @include xl {
                @include fl();
                @include fl-v-centr();
                padding-right: 20px;
            }
        }
        &:last-child {
            @include lg {
                padding-left: 18px;
            }
        }
        @include lg {
            -webkit-flex: 0 1 50%;
            flex: 0 1 50%;
            width: 50%;
            max-width: 50%;
            min-width: 50%;
        }
    }
}
.experts__content {
    font-size: 16px;
    line-height: 1.7777777777777777;
    @include lg {
        font-size: 18px;
    }
}
.experts__title {
    font-size: 24px;
    line-height: 1.333333333333333;
    font-weight: 800;
    color: $dark-1;
    margin-bottom: 32px;
    @include xl {
        font-size: 40px;
        line-height: 1.4;
        margin-bottom: 16px;
    }
}
.experts__text {
    line-height: 1.777777777777778;
    margin-bottom: 32px;
    max-width: 560px;
    @include xl {
        font-size: 18px;
    }
}