/* grid */
.grid {
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-justify-content: flex-start;
    -ms-flex-pack: start;
    justify-content: flex-start;
    -webkit-align-content: stretch;
    -ms-flex-line-pack: stretch;
    align-content: stretch;
}
.b-grid__item {
    -webkit-flex: 0 1 100%;
    flex: 0 1 100%;
    width: 100%;
    max-width: 100%;
    min-width: 100%;
}
.b-grid__inner {
    height: 100%;
}
.grid__item {
    -webkit-flex: 0 1 100%;
    flex: 0 1 100%;
    width: 100%;
    max-width: 100%;
    min-width: 100%;
    height: auto;
}
.grid--gap {
    margin-right: -16px;
    margin-left: -16px;
    & > .grid__item {
        padding-right: 16px;
        padding-left: 16px;
    }
}
.grid--lg-2 {
    .grid__item {
        @include lg {
            -webkit-flex: 0 50%;
            flex: 0 50%;
            width: 50%;
            max-width: 50%;
            min-width: 50%;
        }
    }
}
.grid--sm-2 {
    .grid__item {
        @include sm {
            -webkit-flex: 0 50%;
            flex: 0 50%;
            width: 50%;
            max-width: 50%;
            min-width: 50%;
        }
    }
}
.grid--md-2 {
    .grid__item {
        @include md {
            -webkit-flex: 0 50%;
            flex: 0 50%;
            width: 50%;
            max-width: 50%;
            min-width: 50%;
        }
    }

}
