/* home */
/* demo */
.demo {
    @include bg-color-section();
}
.demo__inner {
    background: $soft-gray;
    @include md {
        border-radius: $border-radius-8;
    }
    padding: 64px 0 92px;
    @include xl {
        padding-top: 121px;
        padding-bottom: 130px;
    }
}
.carousel-demo {
    .slick-dots {
        position: absolute;
        bottom: -40px;
        left: 0;
        right: 0;
        margin: auto;
        @include max-xl {
            @include fl();
            @include fl-h-centr();
        }
        @include xl {
            top: 108px;
            right: auto;
            left: -12px;
        }
        li {
            padding-right: 12px;
            padding-left: 12px;
            @include xl {
                padding-top: 12px;
                padding-bottom: 12px;
            }
        }
    }
}
.demo__title {
    color: $dark-1;
    max-width: 750px;
    margin-bottom: 40px;
    text-align: center;
    @include xl {
        margin-right: auto;
        margin-left: auto;
        margin-bottom: 72px;
    }
}
.demo__text {
    @include md {
         padding-left: 40px;
    }
    @include xl {
        padding-left: 111px;
    }
}
.demo__sub-title {
    margin-top: 40px;
    margin-bottom: 24px;
    color: $dark-1;
    @include xl {
        margin-top: 72px;
    }
}
.demo__list {
    font-size: 16px;
    line-height: 1.7777777777777777;
    @include lg {
        font-size: 18px;
    }
    ul {
        max-width: 416px;
        margin-bottom: 40px;
        @include xl {
            margin-bottom: 32px;
        }
    }
}
.demo__img img {
   @include object-fit-cover();
   border-radius: $border-radius-4;
   max-width: 640px;
   @include xl {
       margin-left: 14px;
   }
}
.link-more {
    @include fli();
    @include fl-v-centr();
    color: $violet;
    svg {
        margin-left: 11px;
    }
}
.demo__img {
    text-align: center;
    @include xl {
        -webkit-order: 1;
        -ms-flex-order: 1;
        order: 1;
    }
}

/* review */
.review {
    max-width: 1178px;
    padding-top: 66px;
    padding-bottom: 64px;
    margin-right: auto;
    margin-left: auto;
    @include xl {
        padding-top: 80px;
        padding-bottom: 120px;
    }
}
.carousel-review {
    margin-right: -16px;
    margin-left: -16px;
    width: calc(100% + 32px);
    @include xl {
        margin-right: 0;
        margin-left: 0;
        width: auto;
    }
    .slick-arrow {
        position: absolute;
        bottom: 270px;
        @include sm {
            bottom: 20px;
        }
        @include xl {
            bottom: 67px;
        }
    }
    .slick-prev {
        right: 88px;
        @include xl {
            right: 99px;
        }
    }
    .slick-next {
        right: 16px;
        @include xl {
            right: 28px;
        }
    }
    .slick-slide {
        overflow: hidden;
    }
}
.review__img {
    @include md {
        -webkit-flex: 0 1 45%;
        flex: 0 1 45%;
        width: 45%;
        max-width: 45%;
        min-width: 45%;
    }
}
.review__quote {
    @include md {
        -webkit-flex: 0 1 55%;
        flex: 0 1 55%;
        width: 55%;
        max-width: 55%;
        min-width: 55%;
    }
    .grid__inner {
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
        -ms-flex-direction: column;
        flex-direction: column;
        -webkit-justify-content: flex-end;
        -ms-flex-pack: end;
        justify-content: flex-end;
        height: 100%;
    }
    .quote {
        max-width: 530px;
        min-height: 200px;
        padding-bottom: 20px;
        @include lg {
            min-height: 261px;
        }
    }
    // margin-top: 7.5%;
    margin-bottom: 10px;
    @include sm {
        -webkit-order: 1;
        -ms-flex-order: 1;
        order: 1;
    }
    @include xl {
        margin-bottom: 0;
        padding-left: 75px;
    }
    .quote {
        position: relative;
        margin-left: 16px;
        margin-right: 16px;
        @include xl {
            margin-right: 0;
            margin-left: 0;
        }
    }
 }
.review__info {
    margin-right: 16px;
    margin-left: 16px;
    @include xl {
        margin-right: 0;
        margin-left: 0;
        margin-bottom: 52px;
    }
}
.review__img {
    position: relative;
    padding: 30px 16px 0;
    @include sm {
        padding-bottom: 30px;
    }
    @include md {
        padding-bottom: 0;
    }
    @include xl {
        padding-right: 0;
        padding-left: 0;
        padding-left: 30px;

    }
    &::before {
        content: "";
        position: absolute;
        top: 7px;
        left: -6px;
        width: 35px;
        height: 32px;
        background-color: transparent;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        background-image: url("data:image/svg+xml,%3Csvg width='35' height='32' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M34 11.678V1M11.66 31L1 30.804M18.769 15.746l-7.54-7.55' stroke='%236665DD' stroke-width='2' stroke-linecap='round'/%3E%3C/svg%3E");
        @include xl {
            top: 0;
            left: 0;
        }
    }
    img {
        overflow: hidden;
        border-radius: $border-radius-4;
        max-height: 222px;
        @include md {
            max-height: 100%;
        }
    }
}
.review__name {
    display: block;
    margin-bottom: 4px;
    color: $dark-1;
}
.review__post {
    display: block;
}
