/* telephone numbers select */
.form {
    .iti {
        width: 100%;
    }
    .iti__flag-box {
        width: auto;
    }
    .iti__flag {
        display: none !important;
    }
    .iti__selected-dial-code {
        font-size: 14px;
        color: $dark-1;
    }
    .iti--separate-dial-code .iti__selected-flag {
        padding-right: 10px;
        background: none !important;
        &::before {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            width: 1px;
            height: 16px;
            margin: auto;
            background: $light-gray;
        }
    }
    .iti__arrow {
        margin-left: 8px;
        border: none;
        width: 8px;
        height: 6px;
        background-image: url("data:image/svg+xml,%3Csvg width='8' height='6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.707 4.793l2.586-2.586C7.923 1.577 7.477.5 6.586.5H1.414C.524.5.077 1.577.707 2.207l2.586 2.586a1 1 0 001.414 0z' fill='%2318223A'/%3E%3C/svg%3E");
        background-color: transparent;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        @extend %transition-all-150;
    }
    .iti__arrow--up {
            -webkit-transform: scale(1) rotate(180deg);
        transform: scale(1) rotate(180deg);
    }
    .iti__country-list {
        font-size: 14px;
        line-height: 1.645;
        color: $dark-1;
        width: 270px;
        white-space: normal;
        margin-top: 8px;
        margin-left: 0;
        box-shadow: none;
        border-color: $light-gray;
        max-height: 250px;
        border-radius: $border-radius-4;
    }
    .iti__country {
        padding: 6px 11px;
    }
    .iti__country.iti__highlight {
        background-color: $soft-gray;
    }
}
.iti-mobile {
    .iti__flag-box {
        display: none !important;
    }
    .iti--container {
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        width: auto;
        background: rgba(29, 36, 54, .6);
    }
    .iti__country-list {
        width: calc(100% - 32px);
        top: 16px;
        bottom: 16px;
        left: 16px;
        right: 16px;
        margin: 0;
        box-shadow: 0 16px 32px rgba(232, 232, 239, 0.16), inset 0 2px 0 $soft-violet;
        border-color: $light-gray;
        border-radius: $border-radius-4;
    }
    .iti__country.iti__highlight {
        background-color: $soft-gray;
    }
    .iti__country-name {
        color: $dark-1;
    }
    .iti__divider {
        border-bottom-color: $light-gray;
    }
}

/* multiple select */
.form {
    .ms-parent {
        display: block;
        width: 100% !important;
    }
    .ms-choice {
        height: auto;
        padding: 11px 16px;
        overflow: hidden;
        cursor: pointer;
        border-color: $light-gray;
        line-height: 1.5;
        color: $dark-1;
        &:active,
        &:focus {
            color: $violet;
            border-color: $violet;
        }
    }
    .ms-choice>span {
        position: static;
        padding-left: 0;
    }
    .ms-choice>div.icon-caret {
        right: 20px;
        border: none;
        width: 8px;
        height: 6px;
        background-image: url("data:image/svg+xml,%3Csvg width='8' height='6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.707 4.793l2.586-2.586C7.923 1.577 7.477.5 6.586.5H1.414C.524.5.077 1.577.707 2.207l2.586 2.586a1 1 0 001.414 0z' fill='%2318223A'/%3E%3C/svg%3E");
        background-color: transparent;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        @extend %transition-all-150;
    }
    .ms-choice>div.icon-caret.open {
        -webkit-transform: scale(1) rotate(180deg);
        transform: scale(1) rotate(180deg);
    }
    .ms-drop {
        right: 0;
        min-width: 240px;
        margin-top: 8px;
        color: $dark-1;
        border-color: $light-gray;
        box-shadow: none;
    }
    .ms-drop ul>li.hide-radio:focus,
    .ms-drop ul>li.hide-radio:hover,
    .ms-drop ul>li.hide-radio.selected {
        color: $dark-1;
        background-color: $soft-gray;
    }
    .ms-drop ul>li.hide-radio label {
        padding: 6px 11px;
    }
    .ms-drop ul>li.hide-radio:focus label,
    .ms-drop ul>li.hide-radio:hover label,
    .ms-drop ul>li.hide-radio.selected label {
        font-weight: bold;
    }
    .ms-drop ul>li {
        margin-bottom: 0;
    }
}
