/* privacy */
.privacy {
    padding-top: 143px;
}
.privacy__top {
    max-width: 1200px;
    margin-bottom: 40px;
    @include xl {
        @include fl();
        @include fl-v-centr();
        @include fl-between();
        margin-bottom: 16px;
        padding-left: 336px;
    }
    img {
        @include max-xl {
            display: none;
        }
    }
}
.privacy__title {
    font-size: 36px;
    font-weight: 800;
    line-height: 1.166666666666667;
    color: $dark-1;
    @include xl {
        font-size: 56px;
        line-height: 1.142857142857143;
    }
}
.privacy__main {
    @include xl {
        @include fl();
    }
}
.privacy__content {
    font-size: 18px;
    line-height: 1.777777777777778;
    max-width: 936px;
    @include xl {
        padding-left: 72px;
    }
    h2 {
        font-size: 24px;
        font-weight: 800;
        line-height: 1.333333333333333;
        color: $dark-1;
        margin-bottom: 16px;
    }
    h3,
    strong {
        font-size: 18px;
        font-weight: 800;
        line-height: 28px;
        color: $dark-1;
    }
    h3 {
        margin-bottom: 16px;
    }
    strong {
    }
    p {
        & + h2,
        & + h3 {
            margin-top: 40px;
        }
    }
}
.privacy__sidebar {
    position: relative;
    @include xl {
        -webkit-flex: 0 1 264px;
        flex: 0 1 264px;
        width: 264px;
        max-width: 264px;
        min-width: 264px;
    }
    .js-sticky-block {
        font-size: 14px;
        line-height: 1.714285714285714;
        @include max-xl {
            position: static !important;
            border-bottom: 1px solid $soft-gray-2;
            margin-bottom: 32px;
        }
        @include xl {
            border-right: 1px solid $soft-gray-2;
        }
    }
    li {
        margin-bottom: 16px;
    }
    a {
        font-size: inherit;
        color: inherit;
        &:hover {
            font-weight: bold;
        }
        &.is-active {
            font-weight: bold;
            color: $dark-1;
        }
    }
}
.is-stuck {
    position: fixed;
    top: 20px;
    z-index: 1;
}
