/* faq */
.faq {
    margin-bottom: 88px;
}
.faq__title {
    font-weight: 800;
    font-size: 40px;
    line-height: 1.4;
    color: $dark-1;
    margin-bottom: 23px;
}
.faq__item {
    padding-top: 22px;
    padding-bottom: 17px;
    border-bottom: 1px solid $soft-gray-2;
}
.faq__body {
    font-size: 14px;
    line-height: 1.7;
    @include lg {
        font-size: 18px;
        line-height: 1.8;
    }
    display: none;
    padding-top: 16px;
    @include lg {
        padding-top: 40px;
    }
}
.faq__link {
    font-size: 16px;
    font-weight: 800;
    line-height: 1.25;
    display: inline-block;
    color: $violet;
    margin-top: 24px;
}
.faq__head {
    font-size: 16px;
    line-height: 1.8;
    color: $cyan;
    padding-right: 40px;
    @include lg {
        font-size: 18px;
    }
    position: relative;
    cursor: pointer;
    &::after {
        content: "";
        position: absolute;
        top: -3px;
        right: -3px;
        bottom: 0;
        width: 24px;
        height: 24px;
        margin-top: auto;
        margin-bottom: auto;
        background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12 4v16m-8-8h16' stroke='%230B1D49' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
        background-color: transparent;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        @extend %transition-all-150;
    }
    &.is-active {
        &::after {
                -webkit-transform: scale(1) rotate(45deg);
            transform: scale(1) rotate(45deg);
        }
        & + .js-faq-target {
            display: block;
        }
    }
}
