/* templ */
*,
*::before,
*::after {
  box-sizing: border-box;
}
html{
    height: 100%;
}
body {
    font-family: $font-family-base;
    font-size: $font-size-root;
    color: $dark-2;
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
    height: 100%;
}
.main{
    overflow: hidden;
    flex: 1 0 auto;
}
.container {
    margin-right: auto;
    margin-left: auto;
    padding-right: 16px;
    padding-left: 16px;
    max-width: $container-main;
    @include sm {
        width: 540px;
    }
    @include md {
        width: 720px;
    }
    @include lg {
        width: 960px;
    }
    @include xl {
        width: 1140px;
    }
    @include xxl {
        width: 100%;
    }
}

.container--prl-0 {
    @include max-xl {
        padding-right: 0;
        padding-left: 0;
    }
}
.container-narrow {
    max-width: $container-narrow;
    margin-right: auto;
    margin-left: auto;
}

//@include bg-color-section();
@mixin bg-color-section() {
    @include md {
        padding-right: 8px;
        padding-left: 8px;
    }
    @include xxl {
        padding-right: 48px;
        padding-left: 48px;
    }
}
picture {
    display: block;
    img {
        display: block;
    }
}
.img-fluid {
    max-width: 100%;
    width: auto;
    height: auto;
}
.hidden {
    opacity:0;
}
.visible {
    opacity:1;
}
