/* fancy */
.fancybox-bg {
    background: #1d2436 !important;
    opacity: .6 !important;
}
.fancybox-slide--html .fancybox-close-small {
    color: $white;
    padding: 0;
    right: 0;
    top: -40px;
    opacity: 1;
    width: 32px;
    height: 32px;
    @include xxl {
        top: 0;
        right: -60px;
    }
}
.fancybox-slide--html {
    @include max-xxl {
       padding-top: 46px;
    }
}
.fancybox-content {
    overflow: visible;
    @include max-md {
        width: 100%;
    }
    background: transparent;
    padding: 0;
    img {
        display: block;
        margin-right: auto;
        margin-left: auto;
        @media (max-width: 639px) {
            @include object-fit-cover();
        }
    }
}
.modal__content {
    background: $soft-violet;
    border-radius: $border-radius-4;
    padding: 72px 16px;
    @include xxl {
        padding: 64px;
    }
}
