/* type */
.heading-1 {
    font-size: 36px;
    font-weight: 800;
    line-height: 1.1428571428571428;
    @include lg {
        font-size: 56px;
    }
}
.heading-2 {
    font-size: 24px;
    font-weight: 800;
    line-height: 1.4;
    @include lg {
        font-size: 40px;
    }
}
.heading-3 {
    font-size: 24px;
    font-weight: 700;
    line-height: 1.25;
    @include lg {
        font-size: 32px;
        font-weight: 800;
    }
}
.heading-4 {
    font-size: 16px;
    font-weight: 800;
    line-height: 1.25;
}
.text-1 {
    font-size: 24px;
    font-weight: 500;
    line-height: 1.3333333333333333;
}
.text-2 {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.7777777777777777;
    @include lg {
        font-size: 18px;
    }
}
.text-3 {
    font-size: 14px;
    font-weight: 400;
    @include lg {
        font-size: 16px;
    }
}
.text-4 {
    font-size: 14px;
    font-weight: 400;
    line-height: 1.7142857142857142;
}
.label-1 {
    font-size: 18px;
    font-weight: 800;
    line-height: 1.5555555555555556;
}
.label-2 {
    font-size: 18px;
    font-weight: 600;
}
.label-3 {
    font-size: 14px;
    font-weight: 700;
    @include lg {
        font-size: 16px;
    }
}
.label-4 {
    font-size: 14px;
    font-weight: 700;
}
p {
    &:not(:last-child) {
        margin-bottom: 16px;
    }
}