/* menu-card */
.menu-card {
    display: block;
    padding: 16px;
    border-radius: $border-radius-4;
    @include md {
        max-width: 304px;
        margin-right: auto;
        margin-left: auto;
        padding: 32px;
    }
    &:not(:last-child) {
        margin-bottom: 16px;
        @include lg {
            margin-bottom: 0;
        }
    }
}
.menu-card--bg-1 {
    background: $soft-violet;
}
.menu-card--bg-2 {
    background: $soft-green;
}
.menu-card__icon {
    margin-bottom: 24px;
}
.menu-card__title {
    display: block;
    margin-bottom: 8px;
    color: $dark-1;
}
.menu-card__text {
    display: block;
    color: $dark-2;
}
