/* info */
.info {
    @include bg-color-section();
    @include md {
        padding-bottom: 64px;
        @include xl {
            padding-bottom: 175px;
        }
    }
}
.info__inner {
    position: relative;
    margin-bottom: 72px;
    padding-top: 40px;
    padding-bottom: 40px;
    @include sm {
        padding-top: 60px;
        padding-bottom: 60px;
    }
    @include md {
        margin-bottom: 0;
        padding-top: 0;
        padding-bottom: 0;
    }
    .body-violet & {
        background: $soft-violet;
        @include md {
            background: none !important;
        }
    }
    .body-green & {
        background: $soft-green;
        @include md {
            background: none !important;
        }
    }
    &::before {
        position: absolute;
        border-radius: $border-radius-8;
        top: 160px;
        left: 0;
        right: 0;
        bottom: 160px;
        margin: auto;
        @include md {
            content: "";
        }
        .body-violet & {
            background: $soft-violet;
        }
        .body-green & {
            background: $soft-green;
        }
    }
    .container--info {
            max-width: 1187px;
    }
}
.info__title {
    font-size: 16px;
    line-height: 1.75;
    @include lg {
        font-size: 18px;
        line-height: 1.777777777777778;
    }
    text-align: center;
    margin-bottom: 40px;
    @include lg {
        margin-bottom: 64px;
    }
}
.info__sub-title {
    font-weight: 800;
    font-size: 36px;
    line-height: 1.166666666666667;
    color: $dark-1;
    margin-bottom: 16px;
    @include lg {
        font-size: 56px;
        line-height: 1.142857142857143;
    }
}
.info__list {
    position: relative;
    z-index: 1;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
}
.info__item {
    position: relative;
    max-width: 1008px;
    background: $white;
    box-shadow: 0 8px 32px rgba(227, 227, 235, .24);
    border-radius: $border-radius-4;
    padding: 24px 16px;
    &:not(:last-child) {
        margin-bottom: 16px;
        @include md {
            margin-bottom: 40px;
        }
    }
    @include md {
        display: -webkit-flex;
        display: flex;
    }
    @include md {
        -webkit-align-items: center;
        -ms-flex-align: center;
        align-items: center;
        padding: 48px;
    }
    &:nth-child(even) {
        -webkit-align-self: flex-end;
        -ms-flex-item-align: end;
        align-self: flex-end;
    }
    picture {
        margin-bottom: 16px;
        @include md {
            -webkit-flex: 1 1 auto;
            -ms-flex: 1 1 auto;
            flex: 1 1 auto;
            min-width: 245px;
            max-width: 245px;
            margin-bottom: 0;
        }
        img {
            width: 100%;
            @include md {
                width: auto;
            }
        }
    }
}
.info__item-title {
    font-size: 24px;
    line-height: 1.25;
    font-weight: 700;
    color: $dark-1;
    max-width: 500px;
    margin-bottom: 8px;
    @include lg {
        margin-bottom: 17px;
        font-size: 32px;
    }
}
.info__num {
    position: absolute;
    top: 28px;
    right: 24px;
    font-size: 18px;
    font-weight: 800;
    line-height: 1;
    display: none;
    @include md {
        display: block;
    }
    .body-violet & {
        color: $soft-violet;
    }
    .body-green & {
        color: $soft-green;
    }
}
.info__caption {
    font-size: 14px;
    line-height: 1.714285714285714;
    @include lg {
        font-size: 18px;
        line-height: 1.777777777777778;
    }
    @include md {
        padding-left: 30px;
    }
    @include xl {
        padding-left: 75px;
    }
}
