/* footer */
.footer {
    flex: 0 0 auto;
    padding-top: 64px;
    padding-bottom: 28px;
    @include xl {
        padding: 120px 0;

    }
}
.footer__inner {
    @include xl {
        @include fl();
        @include fl-between();
    }
}
.footer__logo {
    display: block;
    width: 108px;
    margin-bottom: 20px;
}
.footer-address {
    max-width: 304px
}
.footer-address__text {
    margin-bottom: 32px;
    color: $gray;
}
.footer-address__link {
    color: $violet;
}
.footer-menu {
    @include fl();
    padding-top: 27px;
    @include xl {
        margin-top: -4px;
        margin-left: -21px;
    }
}
.footer-menu__list {
    columns: 2;
    @include md {
        columns: 3;
    }
    @include xl {
        columns: 2;
    }
    column-gap: 60px;
    & > .footer-menu__item {
        margin-bottom: 24px;
        & > .footer-menu__link {
            font-size: 14px;
            font-weight: 700;
            line-height: 1.5;
            @include lg {
                font-size: 16px;
            }
        }
    }
}
.footer-menu__sub-list {
    padding-top: 8px;
    .footer-menu__item {
        &:not(:last-child) {
            margin-bottom: 8px;
        }
    }
}
.footer-menu__item {
    color: $dark-1;
}
.footer-menu__link {
    color: inherit;
}
.footer__contact {
    padding-top: 27px;
    @include xl {
        position: relative;
        left: 5px;
    }
}
.form-contact {
    margin-bottom: 34px;
}
.form-contact__title {
    color: $dark-1;
    margin-bottom: 10px;
}
.form-contact__inner {
    position: relative;
    max-width: 400px;
}
.form-contact__input {
    font-size: 14px;
    line-height: 1.7142857142857142;
    border: 1px solid $light-gray;
    border-radius: $border-radius-4;
    padding: 11px 49px 11px 16px;
    width: 100%;
    min-width: 304px;
    box-shadow: none;
    background: $white;
}
.form-contact__btn {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    border: none;
    background: none;
    padding-right: 15px;
    padding-left: 15px;
    &:hover {
        path {
            fill: $blue;
        }
    }
}
