/* quote */
.quote {
    font-size: 24px;
    font-weight: 500;
    line-height: 1.3333333333333333;
    color: $dark-1;
    position: relative;
    padding-top: 49px;
    &::before {
        content: "";
        position: absolute;
        top: 2px;
        left: 0;
        width: 20px;
        height: 16px;
        background-color: transparent;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        background-image: url("data:image/svg+xml,%3Csvg width='20' height='16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8.027 8.099V16H0v-5.333c0-2.063.357-3.95 1.07-5.663C1.784 3.292 2.965 1.624 4.615 0L7.56 2.239c-1.918 2.107-3.01 4.06-3.278 5.86h3.746zm11.973 0V16h-8.027v-5.333c0-2.063.357-3.95 1.07-5.663.714-1.712 1.896-3.38 3.546-5.004l2.943 2.239c-1.918 2.107-3.01 4.06-3.278 5.86H20z' fill='%2351D8DE'/%3E%3C/svg%3E");
    }
}
