.hero__title {
    color: $dark-1;
}
.hero__1 {
    padding-bottom: 70px;
    .body-green & {
        @include xl {
            padding-bottom: 107px;
        }
    }
    .grid__inner {
        max-width: 580px;
    }
    .grid__item {
        &:first-child {
            margin-bottom: 40px;
            @include xl {
                margin-bottom: 0;
            }
        }
    }
    .grid__item--text {
        @include lg {
            padding-right: 20px;
        }
        @include xxl {
            padding-right: 0;
        }
    }
    .grid__item--img {
        @include lg {
            -webkit-flex: 0 1 52%;
            flex: 0 1 52%;
            width: 52%;
            max-width: 52%;
            min-width: 52%;
        }
        img {
            @media (max-width: 639px) {
                width: 100%;
            }
        }
    }
    .grid__item--text {
        @include lg {
            -webkit-flex: 0 1 48%;
            flex: 0 1 48%;
            width: 48%;
            max-width: 48%;
            min-width: 48%;
        }
    }
    .hero__title {
        position: relative;
        z-index: 1;
        margin-bottom: 16px;
        br {
            @include max-xl {
                display: none;
            }
        }
        @include xl {
            margin-top: 54px;
        }
        @media (min-width: 992px) and (max-width: 1199px){
            font-size: 50px;
        }
        span {
            position: relative;
            display: inline-block;
            white-space: nowrap;
            padding-left: 3px;
            padding-right: 3px;
            &::after {
                content: "";
                position: absolute;
                right: 0;
                bottom: 0;
                left: 0;
                z-index: -1;
                height: 16px;
                border-radius: $border-radius-1;
                .body-violet & {
                    background: $soft-violet;
                }
                .body-green & {
                    background: $soft-green-2;
                }
            }
            .body-violet & {
                color: $violet;
            }.body-green & {
                color: $blue;
            }
        }
    }
    img {
        @include xxl {
            max-width: 672px;
            margin-left: 10px;
        }
    }
}
.hero__2 {
    padding-bottom: 64px;
    @include xl {
        padding-top: 99px;
        padding-bottom: 102px;
    }
    .grid__inner {
        max-width: 640px;
        @include xl {
            padding-left: 98px;
        }
    }
    .hero__title {
        margin-bottom: 16px;
        @include xl {
            margin-top: 112px;
            margin-bottom: 0;
        }
    }
    .hero__description {
        @include lg {
            padding-right: 20px;
        }
        @include xxl {
            padding-right: 0;
        }
    }
    .hero__text {
        @include xl {
            margin-top: 17px;
        }
    }
    .link-video {
        margin-bottom: 64px;
    }
}
.video {
    position: relative;
    @include md {
        display: inline-block;
    }
}
.hero__description {
    @include xl {
        -webkit-order: 1;
        -ms-flex-order: 1;
        order: 1;
    }
}
