    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    overflow: hidden;
    width: 100px;
    height: 100px;
    border-radius: $border-radius-round;
    border: none;
    box-shadow: none;
    background-image: url("data:image/svg+xml,%3Csvg width='100' height='100' viewBox='0 0 100 100' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M67.1278 44.3242L36.4919 26.4378C33.8253 24.8809 30.4751 26.8043 30.4751 29.8922V64.6129C30.4751 67.6546 33.7356 69.5833 36.4014 68.1185L67.0373 51.2842C69.7614 49.7873 69.812 45.8914 67.1278 44.3242Z' fill='%236665DD'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M33.325 29.6821C33.325 27.482 35.7119 26.1116 37.6119 27.2208L76.0463 49.6603C77.9588 50.7769 77.9227 53.5527 75.9818 54.6192L37.5474 75.7387C35.6481 76.7824 33.325 75.4082 33.325 73.241V29.6821ZM74.6093 52.1215L36.1749 29.6821V73.241L74.6093 52.1215Z' fill='%2318223A'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M61.7379 3.97321C50.747 1.17025 39.1158 2.3929 28.9479 7.42005C18.78 12.4472 10.7467 20.9469 6.30079 31.3821C1.85492 41.8173 1.28999 53.4989 4.70818 64.3144C8.12637 75.1298 15.302 84.3651 24.9374 90.3499C34.5727 96.3348 46.0317 98.6741 57.2418 96.9448C68.452 95.2155 78.6733 89.5317 86.0572 80.9214C93.1694 72.6279 97.1969 62.1465 97.4836 51.2501C97.5017 50.56 98.0596 50.0001 98.75 50.0001C99.4404 50.0001 100.002 50.5599 99.9844 51.2501C99.6972 62.7436 95.4558 73.8021 87.9549 82.5489C80.1824 91.6123 69.4232 97.5952 57.623 99.4155C45.8228 101.236 33.7608 98.7735 23.6183 92.4736C13.4758 86.1738 5.92249 76.4525 2.3244 65.0677C-1.2737 53.6831 -0.679036 41.3866 4.00083 30.4022C8.6807 19.4178 17.1368 10.4707 27.8399 5.17899C38.5429 -0.112743 50.7863 -1.39974 62.3557 1.55074C73.521 4.39814 83.3472 11.0107 90.1874 20.2516C90.5982 20.8065 90.4606 21.5872 89.8956 21.9839C89.3306 22.3807 88.5523 22.2431 88.1408 21.6888C81.6442 12.9362 72.3247 6.67307 61.7379 3.97321Z' fill='black'/%3E%3C/svg%3E%0A");
    background-color: transparent;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
@media (max-width: 639px) {
    .btn-video {
        display: none;
    }
}
.hero__text {
    margin-bottom: 32px;
    @include xl {
        margin-top: 16px;
        margin-bottom: 40px;
    }
}
.link-video {
    color: $violet;
    @include fli();
    @include fl-v-centr();
    svg {
        margin-left: 14px;
    }
}
