/* header */
.header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    flex: 0 0 auto;
    width: 100%;
    z-index: 1000;
    padding-top: 18px;
    padding-bottom: 16px;
}
.header__inner {
    @include fl();
    @include fl-between();
    @include fl-v-centr();
}
.header-nav__menu {
    @include xl {
        @include fl();
        @include fl-h-centr();
    }
}
//@include menu-arr();
@mixin menu-arr() {
    -webkit-transform: scale(1) rotate(180deg);
    transform: scale(1) rotate(180deg);
    background-image: url("data:image/svg+xml,%3Csvg width='8' height='6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.707 4.793l2.586-2.586C7.923 1.577 7.477.5 6.586.5H1.414C.524.5.077 1.577.707 2.207l2.586 2.586a1 1 0 001.414 0z' fill='%236665DD'/%3E%3C/svg%3E");
}
//@include menu-line();
@mixin menu-line() {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 2px;
    background: $violet;
}
.header-nav__item {
    padding-top: 32px;
    padding-bottom: 32px;
    @include xl {
        padding-top: 0;
        padding-bottom: 0;
    }
    &:not(:last-child) {
        @include xl {
            margin-right: 56px;
        }
    }
    &:hover {
        .header-nav__link {
            @include xl {
                color: $violet;
            }
            &::before {
                @include xl {
                    content: "";
                }
                @include menu-line();
                @extend %transition-all-150;
            }
        }
        .header-nav__link--menu {
            &::after {
                @include xl {
                    @include menu-arr();
                }
            }
        }
    }
}
.header-nav__link {
    position: relative;
    display: block;
    font-weight: 500;
    font-size: 24px;
    line-height: 1.3333333333333333;
    white-space: nowrap;
    text-decoration: none !important;
    color: $dark-1;
    text-align: center;
    &.is-active {
        &::before {
            @include menu-line();
        }
        &::after {
            @include max-xl {
                @include menu-arr();
            }
        }
    }
    @include max-xl {
        color: $dark-1 !important;
    }
    @include xl {
        font-size: 16px;
        line-height: 1.5;
        padding-top: 12px;
        padding-bottom: 12px;
        text-align: left;
    }
}
.header-nav__link--menu {
    @include xl {
        padding-right: 21px;
    }
    &::after {
        content: "";
        display: inline-block;
        vertical-align: middle;
        margin-left: 16px;
        width: 8px;
        height: 6px;
        background-image: url("data:image/svg+xml,%3Csvg width='8' height='6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.707 4.793l2.586-2.586C7.923 1.577 7.477.5 6.586.5H1.414C.524.5.077 1.577.707 2.207l2.586 2.586a1 1 0 001.414 0z' fill='%2318223A'/%3E%3C/svg%3E");
        background-color: transparent;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        @extend %transition-all-150;
        @include xl {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            margin-top: auto;
            margin-bottom: auto;
        }
    }
}
.header-submenu {
    visibility: hidden;
    height: 0;
    opacity: 0;
    top: 100%;
    @extend %transition-all-150;
    @include xl {
        position: absolute;
        left: 0;
        right: 0;
        padding-top: 0;
    }
}
.header-submenu__inner {
    @include md {
        @include fl();
        @include fl-h-around();
    }
    @include xl {
        position: relative;
        top: 20px;
        padding: 80px 64px;
        background: $white;
        box-shadow: 0 16px 32px rgba(232, 232, 239, .16), inset 0 2px 0 #F0F0FC;
        border-bottom-right-radius: $border-radius-4;
        border-bottom-left-radius: $border-radius-4;
    }
}
@mixin sub-menu-open() {
    visibility: visible;
    height: auto;
    opacity: 1;
}
.header-nav__item {
    &:hover {
        .header-submenu {
            @include xl {
               @include sub-menu-open();
            }
        }
    }
}
.header-nav__link {
    &.is-active {
        & + .header-submenu {
            @include max-xl {
                @include sub-menu-open();
            }
            padding-top: 32px;
            @include xl {
                padding-top: 0;
            }
        }
    }
}
.header-control {
    position: relative;
    z-index: 1;
}
.header-control__log {
    font-weight: 800;
    font-size: 16px;
    line-height: 1.25;
    margin-right: 27px;
    color: $dark-1;
    display: none;
    @include xl {
        display: inline-block;
    }
}
.header-control__sign {
    display: none;
    padding-right: 30px;
    padding-left: 30px;
    @include xl {
        display: inline-block;
    }
}

/* header-nav__control */
.header-nav__control {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    padding: 16px 16px 28px;
    background: $white;
    @include xl {
        display: none;
    }
    .btn {
        display: block;
        width: 100%;
        max-width: 400px;
        margin: 0 auto 27px;
    }
}
@media(max-height: 414px) {
    .header-nav__control {
        padding-bottom: 16px;
    }
}
.header__soc {
    @include fl();
    @include fl-h-centr();
}

/* header-nav */
.header-nav {
    @include max-xl {
        position: fixed;
        z-index: 1002;
        top: 83px;
        bottom: 0;
        right: 100%;
        left: -100%;
        overflow: hidden;
        padding-top: 50px;
        padding-right: 16px;
        padding-left: 16px;
        background: $white;
    }
    @include xl {
        position: relative;
        width:800px;
        left: 47px;
    }
    @media(min-width: 1600px) {
        width: 980px;
    }
    @include xxl {
    }
}
.header-nav--open {
    left: 0;
    right: 0;
}
.header-nav__menu {
    position: absolute;
    top: 40px;
    left: 0;
    right: 0;
    bottom: 0;
    padding-right: 16px;
    padding-left: 16px;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    padding-bottom: 158px;
    @include xl {
        position: static;
        padding-bottom: 0;
    }
}
@media(max-height: 414px) {
    .header-nav__menu {
        top: 0;
    }
}

/* header-nav-toggle */
.header-nav-toggle {
    position: relative;
    z-index: 1003;
    display: block;
    font-size: 0;
    width: 24px;
    height: 24px;
    background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M22 18.005c0 .55-.446.995-.995.995H6.995a.995.995 0 010-1.99h14.01c.55 0 .995.445.995.995zM22 12c0 .55-.446.995-.995.995h-10.01a.995.995 0 110-1.99h10.01c.55 0 .995.446.995.995zM21.005 6.99a.995.995 0 000-1.99H2.995a.995.995 0 100 1.99h18.01z' fill='%23050C26'/%3E%3C/svg%3E");
    background-color: transparent;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    @include xl {
        display: none;
    }
}
.header-nav-toggle--open {
    background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M13.4 12l6.3-6.3c.4-.4.4-1 0-1.4-.4-.4-1-.4-1.4 0L12 10.6 5.7 4.3c-.4-.4-1-.4-1.4 0-.4.4-.4 1 0 1.4l6.3 6.3-6.3 6.3c-.2.2-.3.4-.3.7 0 .6.4 1 1 1 .3 0 .5-.1.7-.3l6.3-6.3 6.3 6.3c.2.2.4.3.7.3.3 0 .5-.1.7-.3.4-.4.4-1 0-1.4L13.4 12z' fill='%23000'/%3E%3C/svg%3E");
}
.header-nav-overlay--locked {
    display: block;
    visibility: visible;
}
.body-is-locked {
    overflow: hidden;
    height: 100%;
    @include xl {
        display: none;
    }
}

/* transition */
.header-nav,
.body-is-locked,
.header-nav-toggle,
.header-nav-toggle--open {
    @extend %transition-all-150;
}
