/* btn */
button {
    @extend %transition-all-150;
}
.btn {
    font-weight: 800;
    font-size: 16px;
    line-height: 1.25;
    padding: 14px 35px;
    background: $violet;
    border-radius: $border-radius-4;
    text-decoration: none !important;
    display: block;
    border: none;
    text-align: center;
    @media(max-width: 399px) {
        width: 100%;
    }
    @media(min-width: 400px) {
        display: inline-block;
    }
    @include xl {
        padding-top: 18px;
        padding-bottom: 18px;
    }
}
.btn--fill-1 {
    color: $white;
    background: $violet;
    @include force-state() {
        background: $blue;
        color: $white;
    }
}
