/* form-confirm */
.form-confirm {
    @include bg-color-section();
    padding-top: 85px;
    height: 100vh;
    min-height: 100%;
    @include xl {
        padding-top: 130px;
    }
}
.form-confirm__inner {
    height: 100%;
    background: $soft-gray;
    @include md {
        border-radius: $border-radius-8;
    }
}
.form-confirm__content {
    text-align: center;
    font-size: 14px;
    line-height: 1.714285714285714;
    color: $dark-1;
    @include xl {
        font-size: 18px;
        line-height: 1.777777777777778;
    }
        max-width: 550px;
    @include xxl {
    }
    img {
        margin: 0 auto 32px;
        @include max-xl {
            max-width: 247px;
        }
        @media (max-height: 414px) {
            max-width: 160px;
        }
        @include xl {
            margin-bottom: 40px;
        }
    }
}
.container--form-confirm {
    @include fl();
    @include fl-h-centr();
    @include fl-v-centr();
    height: 100%;
    padding-top: 20px;
    padding-bottom: 20px;
}
.form-confirm__title {
    font-size: 18px;
    line-height: 1.555555555555556;
    font-weight: 800;
    margin-bottom: 12px;
    @include xl {
        font-size: 36px;
        line-height: 1.166666666666667;
        margin-bottom: 32px;
    }
}
