/* list */
ul {
    &:not([class]) {
        li {
            list-style-position: outside;
            list-style-type: none;
            position: relative;
            margin-bottom: 12px;
            padding-left: 18px;
            text-align: left;
            &::before {
                content: "\2022";
                float: left;
                margin: -.05em 0 0 -1em;
                width: 1em;
                color: $violet;
            }
        }
    }
}
.list-mark {
    ul {
        margin-left: 20px;
        li {
            list-style-position: outside;
            position: relative;
            padding-left: 6px;
            text-align: left;
            &:not(:last-child) {
                margin-bottom: 8px;
            }
            list-style-image: url("data:image/svg+xml,%3Csvg width='12' height='10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M3.372 9.788A.641.641 0 003.84 10a.615.615 0 00.467-.212l7.497-7.855A.703.703 0 0012 1.437a.727.727 0 00-.197-.496L11.096.2a.652.652 0 00-.467-.2.652.652 0 00-.467.2L3.839 6.825l-2-2.083a.641.641 0 00-.468-.211.615.615 0 00-.467.211l-.707.741A.702.702 0 000 5.98a.728.728 0 00.197.497l3.175 3.312z' fill='%230B1D49'/%3E%3C/svg%3E");
            &::before {
                display: none;
                float: left;
                margin: 0 0 0 -1em;
            }
        }
    }
}